.header {
  text-align: center;
  margin-bottom: 2rem;
}

.nav {
  display: flex;
  gap: 1rem;
  margin-bottom: 2rem;
}

.nav a {
  text-decoration: none;
  color: #007BFF;
}

.main {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  width: 100%;
  max-width: 800px;
}

.about-section {
  display: flex;
  flex-wrap: wrap;
  gap: 2rem;
}

.about-content {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
}

.text-content {
  flex: 1;
  margin-right: 1rem;
}

.architecture-image {
  flex: 1;
  text-align: center;
}

.architecture-image img {
  max-width: 100%;
  height: auto;
  border-radius: 10px;
}



.footer {
  margin-top: 2rem;
  text-align: center;
}


.name {
  font-size: 5em;

  font-family: "Times New Roman", serif;
  letter-spacing: -2px;
  transition: 700ms ease;
  font-variation-settings: "wght" 311;
  margin-bottom: 0.8rem;
  color: #000000;
  outline: none;
  text-align: center;
}

.name:hover {
  font-variation-settings: "wght" 582;
  letter-spacing: 4px;
}

.consult-button {
  margin: 25px;
  padding: 25px 30px;
  background-color: #050801;
  color: #d9aa43;
  font-weight: bold;
  border: none;
  border-radius: 5px;
  letter-spacing: 4px;
  overflow: hidden;
  transition: 0.5s;
  cursor: pointer;
}

.consult-button:hover {
  background: #000000;
  color: #d9aa43;
  box-shadow: 0 0 5px #bd8452, 0 0 25px #bd8452, 0 0 50px #bd8452,
      0 0 200px #bd8452;
  /* box-shadow: 0 0 5px #ffe883, 0 0 25px #ffe883, 0 0 50px #ffe883,
      0 0 200px #ffe883;
  -webkit-box-reflect: below 1px linear-gradient(transparent, #0005); */
}



.contact-form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.contact-form label {
  display: flex;
  flex-direction: column;
}

.contact-form input,
.contact-form textarea {
  padding: 0.5rem;
  border: 1px solid #ddd;
  border-radius: 5px;
}

.contact-form button {
  background-color: #bd84529e;
  color: white;
  border: none;
  padding: 0.5rem 1rem;
  font-size: 1rem;
  border-radius: 5px;
  cursor: pointer;
}

.contact-form button:hover {
  background-color: #bd845286;
}

/* Media Queries for Responsiveness */
@media (max-width: 1024px) {
  .service-card {
    flex: 1 1 calc(50% - 1rem); /* Two cards per row on medium screens */
  }
}

@media (max-width: 768px) {
  .service-card {
    flex: 1 1 100%; /* One card per row on small screens */
  }
}
/* Work Section Styles */
.work-section {
  padding: 4rem 2rem;
  background: linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.8)), url('./extras/samson-ZGjbiukp_-A-unsplash.jpg');
  background-attachment: fixed;
  background-size: cover;
  color: #fff;
  text-align: center;
}

.work-title {
  font-size: 2.5rem;
  font-family: "Times New Roman", serif;
  margin-bottom: 2rem;
  color: #d9aa43;
  text-transform: uppercase;
  letter-spacing: 2px;
}

.work-gallery {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
  padding: 1rem;
}

.work-item {
  background: rgba(255, 255, 255, 0.1);
  border-radius: 15px;
  overflow: hidden;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  position: relative;
}

.work-item::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  opacity: 0;
  transition: opacity 0.3s ease;
}

.work-item:hover {
  transform: translateY(-10px);
  box-shadow: 0 15px 30px rgba(0, 0, 0, 0.5);
}

.work-item:hover::before {
  opacity: 1;
}

.work-item-content {
  padding: 2rem;
  position: relative;
  z-index: 1;
}

.work-item h3 {
  font-size: 1.75rem;
  margin-bottom: 1rem;
  color: #d9aa43;
}

.work-item p {
  font-size: 1rem;
  color: #fff;
  opacity: 0.8;
}

/* Responsive Design */
@media (max-width: 768px) {
  .work-gallery {
    grid-template-columns: 1fr;
  }

  .work-title {
    font-size: 2rem;
  }
}