/* Services Section Styles */
.services-section {
    padding: 4rem 2rem;
    background: linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.8)), url('./extras/samson-ZGjbiukp_-A-unsplash.jpg');
    background-attachment: fixed;
    background-size: cover;
    color: #fff;
    text-align: center;
  }
  
  .services-title {
    font-size: 2.5rem;
    font-family: "Times New Roman", serif;
    margin-bottom: 2rem;
    color: #d9aa43;
    text-transform: uppercase;
    letter-spacing: 2px;
  }
  
  .services-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 2rem;
    padding: 1rem;
  }
  
  .service-card {
    background: rgba(255, 255, 255, 0.1);
    border-radius: 10px;
    padding: 2rem;
    position: relative;
    overflow: hidden;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
  }
  
  .service-card:hover {
    transform: translateY(-10px);
    box-shadow: 0 15px 30px rgba(0, 0, 0, 0.5);
  }
  
  .service-title {
    font-size: 1.75rem;
    color: #d9aa43;
    margin-bottom: 1rem;
    position: relative;
    z-index: 1;
  }
  
  .service-description {
    font-size: 1rem;
    color: #fff;
    opacity: 0.8;
    position: relative;
    z-index: 1;
  }
  
  .service-hover-effect {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(45deg, rgba(217, 170, 67, 0.1), rgba(217, 170, 67, 0.3));
    opacity: 0;
    transition: opacity 0.3s ease;
  }
  
  .service-card:hover .service-hover-effect {
    opacity: 1;
  }
  
  /* Responsive Design */
  @media (max-width: 768px) {
    .services-grid {
      grid-template-columns: 1fr;
    }
  
    .services-title {
      font-size: 2rem;
    }
  }